// ‰Ω†ÁöÑ GitHub Áî®Êà∑ÂêçÂíå‰ªìÂ∫ìÂêç
const GITHUB_USERNAME = 'yang1212';  // ÊõøÊç¢Êàê‰Ω†ÁöÑ GitHub Áî®Êà∑Âêç
const REPO_NAME = 'collection-about';  // ÊõøÊç¢Êàê‰Ω†ÁöÑÊñáÁ´†‰ªìÂ∫ìÂêç

// GitHub Personal Access Token
const GH_TOKEN = process.env.VUE_APP_GH_TOKEN;

// ÊâìÂç∞ÁéØÂ¢ÉÂèòÈáè‰ø°ÊÅØÁî®‰∫éË∞ÉËØï
console.log('ÁéØÂ¢ÉÂèòÈáè‰ø°ÊÅØ:', {
  'VUE_APP_GH_TOKEN ÊòØÂê¶Â≠òÂú®': !!process.env.VUE_APP_GH_TOKEN,
  'TOKEN ÊòØÂê¶ÊúâÊïà': !!GH_TOKEN,
  'TOKEN ÈïøÂ∫¶': GH_TOKEN ? GH_TOKEN.length : 0,
  'process.env keys': Object.keys(process.env).filter(key => key.startsWith('VUE_APP_'))
});

// Âü∫Á°ÄËØ∑Ê±ÇÂ§¥
const BASE_HEADERS = {
  'Accept': 'application/vnd.github.v3+json',
  'User-Agent': 'yang1212.github.io'
};

// Ëé∑ÂèñÂ∏¶ËÆ§ËØÅÁöÑËØ∑Ê±ÇÂ§¥
function getAuthHeaders() {
  const headers = { ...BASE_HEADERS };
  if (GH_TOKEN) {
    headers['Authorization'] = `token ${GH_TOKEN}`;
    console.log('‰ΩøÁî®ËÆ§ËØÅËØ∑Ê±Ç');
  } else {
    console.warn('‚ö†Ô∏è Êú™ÈÖçÁΩÆ GitHub TokenÔºåÂ∞Ü‰ΩøÁî®Êú™ËÆ§ËØÅËØ∑Ê±ÇÔºàÂèØËÉΩÂæàÂø´Ëß¶ÂèäÈôêÂà∂Ôºâ');
  }
  return headers;
}

// ÁõÆÂΩïÊò†Â∞ÑÂÖ≥Á≥ª
const DIRECTORY_MAP = {
  'language': '1-ËØ≠Ë®Ä',
  'framework': '2-Ê°ÜÊû∂',
  'engineering': '3-Â∑•Á®ãÂåñ',
  'dev-tools': '4-Â∑•ÂÖ∑',
  'mix': '5-Ê∑∑Âêà',
  'english': '6-Ëã±ËØ≠',
  'ai': 'AI Á¥†Êùê',
  'ppt': 'PPT'
};

// Â∞ÜË∑ØÁî±ÂèÇÊï∞ËΩ¨Êç¢‰∏∫ÂÆûÈôÖÁöÑÁõÆÂΩïÂêç
function getActualDirectoryName(routePath) {
  if (DIRECTORY_MAP[routePath]) {
    return DIRECTORY_MAP[routePath];
  }
  return routePath;
}

// Ê∑ªÂä†ÈáçËØïÊ¨°Êï∞ÂíåÂª∂ËøüÊó∂Èó¥ÈÖçÁΩÆ
const MAX_RETRIES = 3;
const RETRY_DELAY = 1000; // 1Áßí

// Ê∑ªÂä† API ÈôêÂà∂ÁõëÊéß
let lastRateLimitWarning = 0; // Áî®‰∫éÊéßÂà∂Ë≠¶ÂëäÈ¢ëÁéá
const RATE_LIMIT_WARNING_THRESHOLD = 10; // ÂΩìÂâ©‰ΩôÊ¨°Êï∞‰Ωé‰∫éËøô‰∏™ÂÄºÊó∂ÂèëÂá∫Ë≠¶Âëä
const WARNING_INTERVAL = 5 * 60 * 1000; // 5ÂàÜÈíüÂÜÖ‰∏çÈáçÂ§çË≠¶Âëä

async function checkRateLimit(response) {
  const rateLimit = {
    limit: response.headers.get('x-ratelimit-limit'),
    remaining: parseInt(response.headers.get('x-ratelimit-remaining')),
    reset: response.headers.get('x-ratelimit-reset'),
    used: response.headers.get('x-ratelimit-used')
  };

  // ËÆ∞ÂΩïÂΩìÂâç‰ΩøÁî®ÊÉÖÂÜµ
  console.log('GitHub API ‰ΩøÁî®ÊÉÖÂÜµ:', {
    ÈôêÂà∂: rateLimit.limit,
    Ââ©‰Ωô: rateLimit.remaining,
    ÈáçÁΩÆÊó∂Èó¥: new Date(rateLimit.reset * 1000).toLocaleString()
  });

  // Ê£ÄÊü•ÊòØÂê¶ÈúÄË¶ÅÂèëÂá∫Ë≠¶Âëä
  if (rateLimit.remaining < RATE_LIMIT_WARNING_THRESHOLD) {
    const now = Date.now();
    if (now - lastRateLimitWarning > WARNING_INTERVAL) {
      lastRateLimitWarning = now;
      const resetTime = new Date(rateLimit.reset * 1000);
      const minutesUntilReset = Math.ceil((resetTime - now) / (60 * 1000));
      
      console.warn(`‚ö†Ô∏è GitHub API Ë∞ÉÁî®Ê¨°Êï∞Âç≥Â∞ÜËÄóÂ∞Ω:
        - Ââ©‰ΩôÊ¨°Êï∞: ${rateLimit.remaining}
        - ÈôêÂà∂Ê¨°Êï∞: ${rateLimit.limit}
        - ÈáçÁΩÆÊó∂Èó¥: ${resetTime.toLocaleString()}
        - Á∫¶ ${minutesUntilReset} ÂàÜÈíüÂêéÈáçÁΩÆ`);
    }
  }

  return rateLimit;
}

// ‰øÆÊîπ fetchWithRetry ÂáΩÊï∞
async function fetchWithRetry(url, options = {}, retryCount = 0) {
  try {
    // Á°Æ‰øùÊØè‰∏™ËØ∑Ê±ÇÈÉΩÂ∏¶‰∏äËÆ§ËØÅÂ§¥
    const headers = {
      ...getAuthHeaders(),
      ...(options.headers || {})
    };

    const requestOptions = {
      ...options,
      headers
    };

    console.log('ÂèëÈÄÅ GitHub API ËØ∑Ê±Ç:', {
      url,
      ËÆ§ËØÅÁä∂ÊÄÅ: !!GH_TOKEN,
      ÈáçËØïÊ¨°Êï∞: retryCount
    });

    const response = await fetch(url, requestOptions);
    
    // Ê£ÄÊü• API ÈôêÂà∂ÊÉÖÂÜµ
    const rateLimit = await checkRateLimit(response);
    
    if (response.status === 403) {
      const errorBody = await response.text().catch(() => 'Êó†Ê≥ïËé∑ÂèñÈîôËØØËØ¶ÊÉÖ');
      
      if (errorBody.includes('rate limit exceeded')) {
        const resetTime = new Date(rateLimit.reset * 1000);
        const resetTimeStr = resetTime.toLocaleString();
        const waitMinutes = Math.ceil((resetTime - new Date()) / (1000 * 60));
        
        throw new Error(`GitHub API ÈôêÂà∂Â∑≤Ëææ‰∏äÈôê:
          - ÊØèÂ∞èÊó∂ÈôêÂà∂: ${rateLimit.limit}
          - Â∑≤‰ΩøÁî®: ${rateLimit.used}
          - Ââ©‰Ωô: ${rateLimit.remaining}
          - ÈáçÁΩÆÊó∂Èó¥: ${resetTimeStr}
          - ÈúÄÁ≠âÂæÖ: ${waitMinutes} ÂàÜÈíü
          - TokenÁä∂ÊÄÅ: ${GH_TOKEN ? 'Â∑≤ÈÖçÁΩÆ' : 'Êú™ÈÖçÁΩÆ'}
          - ËØ¶ÁªÜ‰ø°ÊÅØ: ${errorBody}`);
      }
    }

    if (!response.ok) {
      throw new Error(`GitHub API ËØ∑Ê±ÇÂ§±Ë¥•: ${response.status} ${response.statusText}`);
    }

    return response;
  } catch (error) {
    console.error('GitHub API ËØ∑Ê±ÇÂ§±Ë¥•:', {
      url,
      error: error.message,
      retryCount,
      tokenÁä∂ÊÄÅ: GH_TOKEN ? 'Â∑≤ÈÖçÁΩÆ' : 'Êú™ÈÖçÁΩÆ'
    });

    if (retryCount < MAX_RETRIES) {
      const delay = RETRY_DELAY * (retryCount + 1);
      console.log(`Á≠âÂæÖ ${delay}ms ÂêéÈáçËØï...`);
      await new Promise(resolve => setTimeout(resolve, delay));
      return fetchWithRetry(url, options, retryCount + 1);
    }

    throw error;
  }
}

// ‰øÆÊîπÊâÄÊúâ‰ΩøÁî® API ÁöÑÂáΩÊï∞ÔºåÁ°Æ‰øù‰ΩøÁî®Áªü‰∏ÄÁöÑËØ∑Ê±ÇÊñπÂºè
export async function getMarkdownFiles(path = '') {
  try {
    const pathParts = path.split('/').map((part, index) => {
      return index === 0 ? getActualDirectoryName(part) : part;
    });
    
    const encodedPath = pathParts
      .map(part => encodeURIComponent(part))
      .join('/');

    const apiUrl = `${GITHUB_API_BASE}/repos/${GITHUB_USERNAME}/${REPO_NAME}/contents/${encodedPath}`;
    const response = await fetchWithRetry(apiUrl);
    const data = await response.json();
    
    const imageExtensions = ['.jpg', '.jpeg', '.png', '.gif', '.webp'];
    return data.filter(item => 
      !item.name.startsWith('.') && (
        item.type === 'dir' ||
        (item.type === 'file' && (
          item.name.endsWith('.md') ||
          imageExtensions.some(ext => item.name.toLowerCase().endsWith(ext))
        ))
      )
    ).map(item => ({
      name: item.name,
      path: item.path,
      type: item.type,
      download_url: item.download_url
    }));
  } catch (error) {
    console.error('Ëé∑ÂèñÊñá‰ª∂ÂàóË°®Â§±Ë¥•:', error);
    throw error;
  }
}

// Ëé∑ÂèñÂçï‰∏™Êñá‰ª∂ÂÜÖÂÆπ
export async function getFileContent(path) {
  try {
    const pathParts = path.split('/');
    if (pathParts.length > 1) {
      pathParts[0] = getActualDirectoryName(pathParts[0]);
    }
    
    const encodedPath = pathParts
      .map(part => encodeURIComponent(part))
      .join('/');

    const apiUrl = `${GITHUB_API_BASE}/repos/${GITHUB_USERNAME}/${REPO_NAME}/contents/${encodedPath}`;
    const response = await fetchWithRetry(apiUrl);

    const fileInfo = await response.json();
    const downloadUrl = fileInfo.download_url;

    // ‰ΩøÁî® download_url Ëé∑ÂèñÊñá‰ª∂ÂÜÖÂÆπ
    const contentResponse = await fetchWithRetry(downloadUrl);

    const content = await contentResponse.text();
    return {
      content,
      lastUpdated: new Date(fileInfo.sha)
    };
  } catch (error) {
    console.error('Ëé∑ÂèñÊñá‰ª∂ÂÜÖÂÆπÂ§±Ë¥•:', error);
    throw error;
  }
}

// Ëé∑Âèñ‰ªìÂ∫ìÁöÑ Issues ÂàóË°®
export async function getIssues(labels = '') {
  try {
    const params = new URLSearchParams({
      state: 'open',
      labels,
      sort: 'created',
      direction: 'desc'
    });

    const response = await fetchWithRetry(
      `${GITHUB_API_BASE}/repos/${GITHUB_USERNAME}/${REPO_NAME}/issues?${params}`
    );
    return await response.json();
  } catch (error) {
    console.error('Ëé∑Âèñ Issues Â§±Ë¥•:', error);
    throw error;
  }
}

// Ëé∑ÂèñÂçï‰∏™ Issue ËØ¶ÊÉÖ
export async function getIssueDetail(issueNumber) {
  try {
    const response = await fetchWithRetry(
      `${GITHUB_API_BASE}/repos/${GITHUB_USERNAME}/${REPO_NAME}/issues/${issueNumber}`
    );
    return await response.json();
  } catch (error) {
    console.error('Ëé∑Âèñ Issue ËØ¶ÊÉÖÂ§±Ë¥•:', error);
    throw error;
  }
}

// Â∞Ü markdown ÂÜÖÂÆπËß£Êûê‰∏∫ÊñáÁ´†ÂÖÉÊï∞ÊçÆ
export function parseMarkdownMetadata(content) {
  const metadataRegex = /^---\n([\s\S]*?)\n---/;
  const match = content.match(metadataRegex);
  
  if (!match) return { content };

  const metadata = {};
  const metadataContent = match[1];
  const lines = metadataContent.split('\n');
  
  lines.forEach(line => {
    const [key, ...values] = line.split(':');
    if (key && values.length) {
      metadata[key.trim()] = values.join(':').trim();
    }
  });

  const articleContent = content.replace(metadataRegex, '').trim();
  
  return {
    ...metadata,
    content: articleContent
  };
}

